.gallery-link {
  flex: 0 100%;
  text-decoration: none;
  display: block;
}

.gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery-item {
  grid-template-rows: 1fr;
  display: grid;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery-info {
  text-align: center;
  justify-content: center;
  align-items: center;
  column-gap: 8px;
  padding: 7px 5px;
  display: flex;
}

p {
  margin: 0;
}

.load-more {
  margin: 0 auto;
  display: block;
}

